/*This is the animation used to change colors */

@-webkit-keyframes color-transition {
	0% {
		background-color: #d68c45;
		border-color: #9fd356;
	}
	33% {
		background-color: #4c956c;
		border-color: #78a94d;
	}
	66% {
		background-color: #fefee3;
		border-color: #649449;
	}
	100% {
		background-color: #ffc9b9;
		border-color: #507f44;
	}
}

@keyframes color-transition {
	0% {
		background-color: #d68c45;
		border-color: #abb94c;
	}
	33% {
		background-color: #4c956c;
		border-color: #9fd356;
	}
	66% {
		background-color: #fefee3;
		border-color: #78a94d;
	}
	100% {
		background-color: #ffc9b9;
		border-color: #649449;
	}
}
@-webkit-keyframes button-transition {
	0% {
		background-color: #41658a;
		border-color: #9fd356;
	}
	33% {
		background-color: #4c956c;
		border-color: #78a94d;
	}
	66% {
		background-color: #2c0735;
		border-color: #649449;
	}
	100% {
		background-color: #0b3948;
		border-color: #507f44;
	}
}
@keyframes button-transition {
	0% {
		background-color: #41658a;
		border-color: #9fd356;
	}
	33% {
		background-color: #4c956c;
		border-color: #78a94d;
	}
	66% {
		background-color: #2c0735;
		border-color: #649449;
	}
	100% {
		background-color: #0b3948;
		border-color: #507f44;
	}
}


